* {
    box-sizing: border-box;
}

html, body {
    background-color: hsl(30, 5%, 15%);
    color: #FFFFFF;
    font-family: 'Playfair Display', serif;
}

body {
    margin: 0;
}

h2 {
    font-weight: bold;
    font-size: 3em;
    text-align: center;
}

header {
    font-family: 'Nunito Sans', sans-serif;
    font-size: 1em;
    display: flex;
    height: 90px;
    align-items: center;
    z-index: 100;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;

    .logo {
        height: 30px;
        font-family: 'Playfair Display', serif;
        font-weight: bold;
        margin-left: 1rem;
    }
    
    a {
        color: inherit;
        text-decoration: none; /* no underline */
    }

    a:hover {
        text-decoration: underline;
    }
}

.top-section {
    z-index: 50;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: grid;
    place-items: center;

    background-image: url('../img/glass-circle3.jpg');
    background-size: 170%;
    background-position: center;
    background-repeat: no-repeat;

    @media only screen and (min-aspect-ratio: 1/1) {
        background-size: auto 100%;

        @media only screen and (min-width: 1000px) {
            background-size: 1000px;
        }
    }

    h1 {
        text-align: center;
        font-size: 4.375em;
        text-shadow: 0px 4px 4px hsla(0, 0%, 0%, 0.25);

        @media only screen and (min-width: 600px) {
            font-size: 5.625em;
        }
    }

    .scroll-down {
        position: absolute;
        left: 50%;
        bottom: 40px;
        transform: translateX(-50%);
        display: flex;
        flex-direction: column;
        align-items: center;

        div {
            font-size: 22px;
            font-weight: bold;
        }
        
        img {
            margin-top: 10px;
            width: 32px;
        }
    }
}

.about-me {
    background-color: hsl(30, 5%, 12%);
    margin-top: 100vh;
    width: 100%;
    padding-top: 100px;
    padding-bottom: 200px;
    
    .description {
        margin: 0 auto;
        width: calc(100% - 40px);
        max-width: 1000px;
        display: grid;
        grid-template-columns: repeat(5, 1fr);

        img {
            grid-column: 1 / 5;
            width: 100%;

            @media only screen and (min-width: 500px) {
                grid-column: 1 / 3;
            }
        }
    
        div {
            display: contents;
            font-size: 1.125em;

            @media only screen and (min-width: 500px) {
                display: initial;
                grid-column: 3 / 6;
                padding-left: 1em;
            }

            @media only screen and (min-width: 700px) {
                font-size: 1.625em;    
            }

            p {
                grid-column: 2 / 6;
                margin-bottom: 0em;
    
                &:last-child {
                    grid-column: 1 / 5;
                }
    
                b {
                    font-weight: 800;
                }
            }
        }
    }
}

.contact-me {
    padding-top: 100px;
    padding-bottom: 200px;
    background-color: hsl(30, 5%, 5%);

    h2 {
        margin-bottom: 2em;
    }

    .error {
        margin: 0 auto 50px;
        display: none;
        width: calc(100% - 40px);
        max-width: 600px;
        color: hsl(0, 80%, 60%)
    }

    form {
        width: calc(100% - 40px);
        max-width: 600px;
        margin: 0 auto;
        display: grid;
        grid-template-rows: repeat(10, 40px auto);
        
        @media only screen and (min-width: 500px) {
            grid-template-rows: initial;
            grid-template-columns: 2fr 4fr;
            row-gap: 2em;
        }
    }

    label {
        font-size: 18px;
        height: 100%;
        max-height: 3.5rem;
        display: flex;
        align-items: center;
        grid-column: 1 / span 1;
    }

    textarea {
        resize: none;
        min-height: 6.125rem;
    }

    input, textarea {
        font-family: 'Nunito Sans', sans-serif;
        font-size: 1em;
        color: #FFFFFF;
        grid-column: 1 / span 1;
        border: none;
        background-image: none;
        background-color: hsl(30, 5%, 20%);
        padding: 1em 1em;
        border-radius: 7px;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        margin-bottom: 20px;

        @media only screen and (min-width: 500px) {
            grid-column: 2 / span 1;
            margin-bottom: initial;
        }

        &.submit {
            cursor: pointer;

            &:active {
                opacity: 0.8;
            }

            &:disabled {
                opacity: 0.6;
                cursor: default;
            }
        }
    }

    .success {
        display: none;
        margin: 0 auto;
        width: 200px;
    }

}